@import url(https://fonts.googleapis.com/css?family=Raleway:500,600,800&display=swap);

.itemCard, .createCard {
  margin-left: 2%;
  margin-right: 2%;
  margin-top: 10%;
  margin-bottom: 10%; }
  .itemCard .mainCard, .createCard .mainCard {
    background-color: #FAFAFA;
    height: 60px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    border-radius: 10px;
    -webkit-transition: 0.3s;
    transition: 0.3s;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 2%;
    padding-right: 2%;
    z-index: 9;
    position: relative; }
    .itemCard .mainCard p, .createCard .mainCard p {
      margin: 0px; }
    .itemCard .mainCard .cardTitle h3, .createCard .mainCard .cardTitle h3 {
      margin: 0px; }
    .itemCard .mainCard .cardCals, .createCard .mainCard .cardCals {
      text-align: right; }
      .itemCard .mainCard .cardCals .itemCal, .createCard .mainCard .cardCals .itemCal {
        color: #003A55; }
    .itemCard .mainCard .subText, .createCard .mainCard .subText {
      color: #808080;
      font-size: 14px;
      font-style: italic; }
  .itemCard .mainCard:hover, .createCard .mainCard:hover {
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2); }
  .itemCard .subCard, .createCard .subCard {
    background-color: white;
    max-height: 0;
    overflow: hidden;
    -webkit-transition: max-height 0.2s ease-out;
    transition: max-height 0.2s ease-out;
    padding-left: 4%;
    padding-right: 4%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    width: 80%;
    margin: auto;
    z-index: 8;
    position: relative; }
    .itemCard .subCard .nutritionSection, .createCard .subCard .nutritionSection {
      padding-top: 5%;
      -webkit-column-count: 2;
      -moz-column-count: 2;
      column-count: 2; }
    .itemCard .subCard p, .createCard .subCard p {
      margin: 0;
      display: block;
      font-size: 14px; }
      .itemCard .subCard p .nutData, .createCard .subCard p .nutData {
        white-space: nowrap;
        color: #003A55; }
    .itemCard .subCard .cardAction, .createCard .subCard .cardAction {
      padding-top: 3%;
      padding-bottom: 3%;
      margin: auto; }
      .itemCard .subCard .cardAction p, .createCard .subCard .cardAction p {
        text-align: center; }
      .itemCard .subCard .cardAction .add, .createCard .subCard .cardAction .add {
        color: #00AEFF; }
      .itemCard .subCard .cardAction .remove, .createCard .subCard .cardAction .remove {
        color: #E63946; }

.createCard .createInput {
  margin-top: 2%;
  margin-bottom: 2%;
  font-size: 16px;
  width: calc(100% - 6%);
  box-sizing: border-box; }

.searchArea {
  background-color: #80D7FF;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
  padding-top: 3%;
  padding-bottom: 3%;
  margin-top: 5%; }
  .searchArea .arrow-up {
    width: 0;
    height: 0;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 10px solid #80D7FF;
    position: relative;
    top: -17px;
    margin: auto; }
  .searchArea .searchBar {
    text-align: center; }
  .searchArea .searchInput {
    font-size: 16px;
    width: calc(100% - 6%);
    box-sizing: border-box; }

.mealList {
  margin-left: 3%;
  margin-right: 3%;
  flex-basis: 0;
  flex-grow: 1; }
  .mealList .mealHeaderName {
    border-bottom: 2px solid #00AEFF;
    text-transform: uppercase; }
  .mealList .searchControl {
    text-align: center;
    text-transform: uppercase;
    font-weight: 800;
    color: #00AEFF; }

.nutritionSummary {
  text-align: center; }
  .nutritionSummary .nutritionTitle {
    color: #FAFAFA;
    background-color: #003A55;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    cursor: pointer;
    width: 200px;
    padding-top: 1%;
    padding-bottom: 1%;
    border-radius: 10px;
    margin-left: auto;
    margin-right: auto;
    font-size: 16px; }
    @media only screen and (max-width: 768px) {
      .nutritionSummary .nutritionTitle {
        padding-top: 2%;
        padding-bottom: 2%; } }
  .nutritionSummary .nutritionTitle:hover {
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2); }
  .nutritionSummary .active {
    overflow: visible !important; }
    .nutritionSummary .active .nutritionDetails {
      padding-bottom: 5%; }
  .nutritionSummary .collapsableSummary {
    max-height: 0;
    overflow: hidden;
    -webkit-transition: max-height 0.2s ease-out;
    transition: max-height 0.2s ease-out;
    background-color: #003A55;
    color: #FAFAFA;
    box-shadow: inset 0 8px 16px 0 rgba(0, 0, 0, 0.6); }
    .nutritionSummary .collapsableSummary .arrow-up {
      width: 0;
      height: 0;
      border-left: 10px solid transparent;
      border-right: 10px solid transparent;
      border-bottom: 10px solid #003A55;
      position: relative;
      top: -10px;
      margin: auto;
      z-index: 9;
      box-shadow: inset 0 8px 16px 0 rgba(0, 0, 0, 0.6); }
    .nutritionSummary .collapsableSummary .calSub {
      font-size: 10px;
      font-weight: 400; }
    .nutritionSummary .collapsableSummary .resultCal {
      white-space: nowrap; }
    .nutritionSummary .collapsableSummary .nutritionDetails {
      -webkit-column-count: 3;
      /* Old Chrome, Safari and Opera */
      -moz-column-count: 3;
      /* Old Firefox */
      column-count: 3;
      text-align: left;
      margin: auto;
      margin-bottom: 4%;
      padding-left: 3%;
      padding-right: 3%; }
      @media only screen and (min-width: 768px) {
        .nutritionSummary .collapsableSummary .nutritionDetails {
          max-width: 60%; } }
      .nutritionSummary .collapsableSummary .nutritionDetails p {
        display: inline-block;
        font-size: 14px;
        margin-top: 0px;
        margin-bottom: 0px; }
        .nutritionSummary .collapsableSummary .nutritionDetails p .nutData {
          white-space: nowrap; }

.loglist #logDate {
  text-align: center;
  margin-top: 1%;
  margin-bottom: 0px; }

.loglist #logChange {
  text-align: center;
  margin-top: 0px; }
  .loglist #logChange .logChange {
