    display: inline-block; }

.loglist .mealsSection {
  margin: auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: stretch; }
  @media only screen and (max-width: 768px) {
    .loglist .mealsSection {
      flex-direction: column; } }

body {
  background-color: #00AEFF;
  margin: 0px; }

#root {
  min-height: 100%; }

.App {
  background-color: #FAFAFA;
  font-family: 'Raleway', sans-serif;
  color: #050505;
  max-width: 1024px;
  margin: auto;
  padding-top: 2%;
  padding-bottom: 2%;
  min-height: calc(100vh); }
  @media only screen and (max-width: 768px) {
    .App {
      padding-bottom: 20%; } }
  .App #appTitle, .App #titleSubtext {
    margin: 0px;
    text-align: center; }
  .App #appTitle {
    color: #00AEFF; }
  .App #titleSubtext {
